.tms-em-chat-top-menu {
    &.ui.top.menu {
        padding-left: 220px;
        height: 60px;
        @media only screen and (max-width: 767px) {
            .tms-chat-at.tms-hide {
                display: none;
            }
        }
        .item.tms-item:before {
            display: none;
        }
        .right.menu {
            .item.tms-item {
                padding-left: 5px;
                padding-right: 5px;
            }
            .ui.search {
                input {
                    width: 100px;
                    transition: width .15s ease-out 0s;
                }
                i.remove.icon {
                    display: none;
                    position: absolute;
                    right: 0;
                    left: auto;
                }
            }
        }
        @media only screen and (max-width: 767px) {
            padding-left: 0;
        }
        .ui.basic.button {
            box-shadow: none;
        }
    }
}
